<template>
	<div id="loading">
		<img :src="img" alt="">
		<p><slot></slot></p>
	</div>
</template>
<script>
	export default{
		name: 'loading',
		props: {
			font: {
				default: '加载中'
			}
		},
		data(){
			return{
				img: '/static/images/loading.gif'
			}
		}
	}
</script>
<style scoped>
	#loading{
		display: flex;
		justify-content: center;
		align-items: center;
	}
</style>


