<template>
	<div id="box">
		<scroll class="box1">
			<div class='box'>
				<mt-swipe :auto="4000">
				  <mt-swipe-item v-for='(item,index) in data' :key='index'>
				  	<img :src="item.picUrl" alt="">
				  </mt-swipe-item>
				</mt-swipe>
				<loading v-if='!sheet.data'></loading>
				<p v-if='!sheet.data'>暂无数据</p>
				<div class="sheet1" v-if='sheet.data'>
					<h4>热门歌单推荐</h4>
					<ul class="sheet">
						<li v-for='item,index in sheet.data.list'>
							<img v-lazy="item.imgurl" alt="" @click='router(item)'>
							<p class='title'>{{item.creator.name}}</p>
							<p class='font'>{{item.dissname}}</p>
						</li>
					</ul>
				</div>
			</div>
		</scroll>
	</div>
</template>
<script>
	import axios from 'axios';
	import { Swipe, SwipeItem } from 'mint-ui';
	import scroll from '@/components/scroll.vue';
	import loading from '@/components/loading.vue';
	export default{
		mounted(){
			axios.get('/qq/musichall/fcgi-bin/fcg_yqqhomepagerecommend.fcg?_=1557719375787&g_tk=5381&uin=0&format=json&inCharset=utf-8&outCharset=utf-8&notice=0&platform=h5&needNewCode=1').then(data => {
				this.data = data.data.data.slider;
			});
			axios.get('/apis/sheet').then(data =>{
				this.sheet = data.data;
			})
		},
		methods:{
			router(item){
				axios.get('/apis/songList').then(data => {
					data.data.data.forEach(item => {
						let obj = {
							images: `https://y.gtimg.cn/music/photo_new/T002R300x300M000${item.albummid}.jpg?max_age=2592000`,
	                        url: `http://dl.stream.qqmusic.qq.com/http://dl.stream.qqmusic.qq.com/C400${item.songmid}.m4a?guid=3237420644&vkey=3AEA2C41B4BE4A2A3240C022AAA768BB02B179F3CE95FEB5BE9842D6724939C8C694FB5A87020D28392DE1B4FE6F08136533CC52506A406D&uin=0&fromtag=38

`
						}
						this.$set(item,'newData',obj)
					})
					this.$router.push({
						path: '/details',
						name: 'details',
						component: resolve => require(['@/components/details.vue'],resolve),
						params: {
							title: item.dissname,
							imgurl: item.imgurl,
							data: data.data,
							show: 0
						}
					})
				})
			}
		},
		data(){
			return {
				data: [],
				sheet: {}
			}
		},
		components: {
			scroll,
			loading
		}
	}
</script>
<style scoped>
	#box{
		background: rgb(39,39,39);
	}
	.box1{
		height: 100vh;
		overflow: hidden;
	}
	.mint-swipe{
        width: 100vw;
        height: 150px;
        background: skyblue;
    }
    .mint-swipe-items-wrap{
    	width: 500vw;
    	height: 150px;
    }
    .mint-swipe-item{
    	width: 100vw;
    	height: 200px;
    	background: #ccc;
    }
    .mint-swipe-items-wrap div img{
        display: block;
        width: 100vw;
    }
    .sheet1{
    	margin-top: 30px;
    }
    .sheet1 h4{
    	text-align: center;
    	color: rgb(254,202,59);
    }
    .sheet img{
    	width: 70px;
    	float: left;
    	padding-right: 10px;
    }
    .sheet li{
    	height: 70px;
    	box-sizing: border-box;
    	padding-left: 20px;
    }
    .sheet li p{
		font-size: 15px;
		margin-top: 20px;
    }
    .sheet li p.title{
		color: #fff;
    }
    .sheet li p.font{
		color: rgb(109,109,109);
    }
</style>



